import React from 'react'
import './Profile.css'
function Profile() {
    return (
      
<div class="row">


<div class="col-lg-5 d-block mx-auto">
    <div class="media">
        <a class="pull-left" href="/">
            <img class="media-object" src={localStorage.getItem('imgProfile')} alt="profile" style={{width: '100px',height: '100px'}}/>
        </a>
        <div class="media-body">
            <h4 class="media-heading ml-4">  {localStorage.getItem('username')}</h4>
    <h5 className="ml-4">Your email : {localStorage.getItem('email')}</h5>

        <h5 className="ml-4">Plano de saude : <span>Sem plano</span></h5>
        <a className="ml-4" href="#/app/planelist"> Comprar um plano</a>
        </div>
    </div>

</div>



</div>
  
    )
}

export default Profile
